import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service'
import { ModalController } from '@ionic/angular';
import { ImageModalPage } from '../image-modal/image-modal.page';
@Component({
  selector: 'app-live-cam',
  templateUrl: './live-cam.page.html',
  styleUrls: ['./live-cam.page.scss'],
  providers: [HttpService]
})
export class LiveCamPage implements OnInit {
  response:any;
  cameraData:any;
  currentColor:any;
  constructor(public shared: HttpService,private modalController: ModalController) {
    this.currentColor = 'light';
    
   }

  ngOnInit() {
  }


  ionViewDidEnter() {
    if (navigator.onLine) {
            this.shared.ServiceCallLiveCam.ServiceCallRequestLiveCamData().then((res) => { 
              // debugger
              
              this.response=res;
              this.cameraData=this.response.content.livecamera
              
              }, err => {
                
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
          }
        
      }


      openPreview(img) {
        this.modalController.create({
          component: ImageModalPage,
          componentProps: {
            img: img
          }
        }).then(modal => {
          modal.present();
        });
      }

}
